.carregando {
  display: flex;
  margin: 50px auto;
  position: relative;
  justify-content: center;
  height: 30px;
  align-items: center;
}
.carregando span {
  width: 200px;
  height: 2px;
  background-color: #6f4200;
  animation: aumentar 0.5s infinite;
}
@keyframes aumentar {
  0% {
    width: 0;
  }
  100% {
    width: 200px;
  }
}
