.header {
  height: 70px;
  width: 100%;
  background-color: #fdf2e3;
  border: 1px solid #ab926d;
  display: flex;
  align-items: center;
  justify-content: center;
}
.busca {
  display: flex;
  align-items: center;
}
.busca label {
  color: #241600;
  font-size: 16px;
  font-family: Arial, Helvetica, sans-serif;
}
.busca input {
  height: 28px;
  width: 50px;
  border: 1px solid #ab926d;
  margin: 0 25px 0 10px;
  font-size: 16px;
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
}
.busca button {
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  font-family: Arial, Helvetica, sans-serif;
  text-transform: uppercase;
  font-weight: 300;
  color: #fdf2e3;
  width: 127px;
  height: 28px;
  background-color: #6f4200;
  border: none;
  font-size: 14px;
  line-height: 14px;
  border-radius: 2px;
  transition: 0.3s;
}
.busca button svg {
  margin-left: 10px;
}
.busca button:hover {
  cursor: pointer;
  background-color: #241600;
  transition: 0.3s;
}
@media (max-width: 420px) {
  .busca input {
    height: 28px;
    margin: 0 15px 0 10px;
  }
}
