.container {
  display: flex;
  background-color: #fdf2e3;
  border: 1px solid #ab926d;
  margin-top: 80px;
  height: 130px;
}
.titulo {
  flex: 1;
  font-weight: normal;
  display: flex;
  text-align: center;
  align-content: center;
  justify-content: center;
  align-items: center;
}
.titulo h1 {
  font-size: 26px;
  letter-spacing: 1px;
  font-family: 'Merriweather', serif;
  font-weight: 400;
}
.separador {
  flex: 0.1;
  display: flex;
  align-items: center;
}
.separador span {
  display: block;
  height: 105px;
  width: 1px;
  background-color: #6f4200;
}
.texto {
  flex: 1;
  display: flex;
  align-content: center;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  line-height: 18px;
  font-family: Arial, Helvetica, sans-serif;
  padding-right: 10px;
}
@media (max-width: 420px) {
  .container {
    margin-top: 50px;
  }
  .titulo h1 {
    font-size: 22px;
    letter-spacing: 1px;
    font-family: 'Merriweather', serif;
    font-weight: 400;
  }
  .texto {
    font-size: 14px;
    line-height: 16px;
    font-family: Arial, Helvetica, sans-serif;
  }
}
