@import url('https://fonts.googleapis.com/css2?family=Great+Vibes&family=Merriweather:ital,wght@0,300;0,400;0,900;1,700&display=swap');
* {
  list-style: none;
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
}
h1,
h2,
h3,
h4 {
  padding: 0;
  margin: 0;
}
.App {
  max-width: 600px;
  margin: 0 auto;
  /* font-family: 'Great Vibes', cursive; */
  font-family: 'Merriweather', serif;
  color: #241600;
}

[data-anime='surgir'] {
  animation: surgir 0.8s;
}
@keyframes surgir {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@media (max-width: 700px) {
  .App {
    margin: 0 1rem 0 1rem;
  }
}
