.footer {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 55px;
  width: 100%;
  background-color: #4f2f00;
  text-align: center;
  margin-top: 55px;
}
.footer p {
  color: #f4f4f4;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
  font-weight: 100;
}
@media (max-width: 420px) {
  .footer {
    margin-top: 30px;
  }
  .footer p {
    font-size: 13px;
    letter-spacing: 0.2px;
  }
}
