.container {
  background-color: #fdf2e3;
  border: 1px solid #ab926d;
  margin-top: 40px;
  transition: 0.4s;
}
.salmoAtual {
  text-align: center;
  font-size: 16px;
  font-weight: 400;
  margin: 30px 0 15px 0;
}
.info {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.capitulo {
  margin: 30px 0 0 35px;
}
.totalVersos {
  font-size: 14px;
  margin: 30px 35px 0 0;
}
.texto {
  margin: 35px;
}
.texto p {
  font-size: 14px;
  line-height: 18px;
}
.texto span {
  margin-right: 5px;
  font-size: 12px;
  line-height: 12px;
}
.autores {
  font-size: 14px;
  text-align: center;
  margin: 80px 0 20px 0;
  font-weight: 400;
}
.aviso {
  margin-top: 40px;
  text-align: center;
}
.aviso p {
  font-size: 14px;
  margin: 30px 35px 30px 35px;
}
